document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija() {
    const barra = document.querySelector('.header'); // Seleccionamos el Header
    const sobreFestival = document.querySelector('.sobre-festival'); // aca seleccionamos un elemento ( la clase .sobre-festival) y una vez hallamos dado scroll hacia este elemento se va a ejecutar este codigo 
    const body = document.querySelector('body'); // agregamos esto de body, body-scroll para que cuando se ponga fijo no sea de golpe

    window.addEventListener('scroll', function() {  // vamos a escuchar por el scroll que hace en la pantalla y eso ejecutara una funcion un callback

        if( sobreFestival.getBoundingClientRect().bottom < 0 ) { // es menor a 0, quiere decir que ya pasamos el elemento, o sea que ya pasamos haciendo scroll a la clase que seleccionamos antes.
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}


function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal') // Seleccionamos la clase de los enlaces
    enlaces.forEach( enlace => {   // enlaces.forEach : para ir iterando en cada uno de ellos
        enlace.addEventListener('click', function(e){
            e.preventDefault();

/*      console.log(e.target);  target es a lo que le hemos dado click, como le agregamos el addEventListener aca uno de los enlaces, mostrara ese enlace
        console.log(e.target.attributes);  muestra los atributos, si tiene clases haref etc.
        console.log(e.target.attributes.href); traera en la consola href="#lineup" y si queremos traernos solo el #lineup, tenemos que hacer :
        console.log(e.target.attributes.href.value);  El value va a acceder al valor del href y nos traera #lineup.
        Para hacerlo mas corto y terminarlo tenemos que hacer: 
        
        const seccion = document.querySelector(e.target.attributes.href.value); Para que quede mejor personalmente podemos hacer:    */

            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll); // acá ya tenemos una instacia a la seccion que le vamos a dar scroll
            seccion.scrollIntoView({ behavior:'smooth' }); // le agregamos un obj de configuracion como BEHAVIOR es como el comportamiento. Y decirle que utilice 'smooth'
            /* pARA QUE FUNCIONE TENEMOS QUE ELIMINAR EL ID#, SINO LO SIGUE TOMANDO Y EL EFECTO QEU HACE ES EL DE POR DEFOULT, PREVENIMOS LA ACCION CON:
                e.preventDefault(); Lo agregamos arriba de todo */
        })

    });   
}


function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i <= 12; i++ ) { // para que comience a dar nombre a las img/ que se ejecute i mientras sea menor ó igual a 12 / i++ y vamos a ir incrementando de uno en uno
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="./build/img/thumb/${i}.jpg"
                alt="imagen galeria">
        `;
        imagen.onclick = function() { //de esta forma al generarle el elemento, le asignamos un evento a imagen con onclick, que es igual a un callback function
            mostrarImagen(i);
        }   // para pasar parametros lo tenemos que hacer como callback


        galeria.appendChild(imagen);
    }   
}


function mostrarImagen(id) {
    const imagen = document.createElement('picture');
    imagen.innerHTML = `
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="./build/img/grande/${id}.jpg"
            alt="imagen galeria">
    `;


    // Crea el Overlay con la imagen
    const overlay = document.createElement('DIV');  // se crea un div llamado overlay
    overlay.appendChild(imagen);                    // agregamos la imagen
    overlay.classList.add('overlay');               // agregamos la clase para darle estilos con css  

    overlay.onclick = function() {  // al hacer click por fuera se quita de la imagen:
        const body = document.querySelector('body'); // se identifica el body
        body.classList.remove('fijar-body'); //le quitamos la clase
        overlay.remove(); // y eliminamos el overlay
    }


    // Boton para cerrar el Modal
    const cerrarModal = document.createElement('P');
    cerrarModal.textContent = 'X';
    cerrarModal.classList.add('btn-cerrar');
    cerrarModal.onclick = function() {  // Una vez que de click se elimina el overlay, es lo que hace la funcion.
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');
        overlay.remove();
    };
    overlay.appendChild(cerrarModal);

    // Añadirlo al HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}